.img-fluid {
  transform-origin: center;
  animation: scale 2000ms ease-in-out forwards;
  transition: transform .2s;
  position: relative;
}
.img-fluid:hover {
  transform: scale(1.2); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
}

